.lead {

  &__title {
    font-size: 86px;
    line-height: 80px;
    font-weight: 700;
    letter-spacing: -0.02em;
    text-transform: lowercase;
    margin: 0;
    padding: 0;
  }

  &__text {
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    margin: 0 0 20px;
    padding: 0;
    color: rgba(255, 255, 255, 0.6);

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}